/**
 * 激励视频播放页获取配置initKsAdFrame
 * @param client
 * @param { height, leftMargin, rightMargin, bottomMargin }
 */
export const initViewportSize = (
  client,
  { height, leftMargin, rightMargin, bottomMargin, offsetDistanceVertical = 0 },
  isFullScreen
) => {
  const { screenWidth, screenHeight } = client;
  const vmin = Math.min(screenWidth, screenHeight); // 物理像素
  const rem = vmin / 10;
  const defaultFontSizeMultiple = getDefaultFontSizeMultiple();
  document.querySelector('html').style.fontSize = rem / dpr + 'px';
  const npx = (1 / 3750) * 100;
  if (!isFullScreen) {
    initKsAdFrame({
      height: Math.ceil(height * npx * rem * defaultFontSizeMultiple),
      leftMargin: Math.ceil(leftMargin * npx * rem * defaultFontSizeMultiple),
      rightMargin: Math.ceil(rightMargin * npx * rem * defaultFontSizeMultiple),
      bottomMargin: Math.ceil(bottomMargin * npx * rem * defaultFontSizeMultiple),
      offsetDistanceVertical: Math.ceil(
        offsetDistanceVertical * npx * rem * defaultFontSizeMultiple
      ),
    });
  }
};

export function changeAHexToHexA(str) {
  const regColor = /^#([A-Fa-f0-9]{3}|[A-Fa-f0-9]{6}|[A-Fa-f0-9]{8})$/;
  const isHex = regColor.test(str);
  if (!isHex) {
    return '#E4A704FF'; // 兜底
  } else if (str.length < 9) {
    return str;
  } else {
    const opacity = str.slice(1, 3);
    const rgb = str.replace(opacity, '');
    return rgb + opacity;
  }
}

/**
 * 多行文本截取固定长度
 * @param str 文本
 * @param len 截取的长度
 */
export function cutMultiLineString(str, len) {
  let strLength = 0;
  const strLen = str.length;
  let strCut = '';
  for (let i = 0; i < strLen; i++) {
    const a = str.charAt(i);
    strLength++;
    if (escape(a).length > 4) {
      strLength++;
    }
    strCut = strCut.concat(a);
    if (strLength >= len) {
      strCut = strCut.concat('...');
      return strCut;
    }
  }
  if (strLength < len) {
    return str;
  }
}

// 创建视频位置
export const createVideoContent = async ({
  dom,
  containerWidth,
  containerHeight,
}: CreateVideoContentInf) => {
  if (!dom) {
    error('root is not exists');
    changePageStatus(PageStatus.ERROR);
    return;
  }
  const imgStyle = dom.getBoundingClientRect();

  const videoPositionParams = {
    leftMarginRation: Number((imgStyle.left / containerWidth).toFixed(15)),
    topMarginRation: Number((imgStyle.top / containerHeight).toFixed(15)),
    widthRation: Number((imgStyle.width / containerWidth).toFixed(15)),
    heightWidthRation: Number((imgStyle.height / imgStyle.width).toFixed(15)),
    leftMargin: Math.ceil(imgStyle.left * dpr),
    topMargin: Math.ceil(imgStyle.top * dpr),
    width: Math.ceil(imgStyle.width * dpr),
    height: Math.ceil(imgStyle.height * dpr),
  };
  const res = await videoPosition(videoPositionParams);

  if (res.result === 1) {
    return true;
  } else {
    changePageStatus(PageStatus.ERROR);
    return false;
  }
};

// 获取背景图片
export const getImgUrl = (info) => {
  const materialType = info.materialType;
  const materialFeature = info.materialFeature[0];
  const { coverUrl, materialUrl } = materialFeature;
  if (materialType === MaterialType.IMAGE && materialUrl) {
    return materialUrl;
  }
  if (coverUrl) {
    return coverUrl;
  }
  return '';
};

// 获取ICON信息
export const getIcon = (info) => {
  return {
    adMarkIcon: info.adMarkIcon,
    adSourceDescription: info.adSourceDescription,
    adSourceType: info.adSourceType,
    adGrayMarkIcon: info.adGrayMarkIcon,
  };
};

export const getImageUrl = (isVideo: boolean, materialUrl: string, coverUrl: string) => {
  if (!isVideo && materialUrl) {
    return materialUrl;
  }
  return coverUrl || '';
};

export function getDefaultFontSizeMultiple() {
  const d = window.document.createElement('div');
  d.style.width = '1rem';
  d.style.display = 'none';
  const head = window.document.getElementsByTagName('head')[0];
  head.appendChild(d);
  const defaultFontSize =
    parseFloat(window.getComputedStyle(d, null).getPropertyValue('width')) || 16;
  return defaultFontSize / 16;
}

export const performanceMark = (markName: string) => {
  try {
    performance.mark(markName);
  } catch (e) {
    console.warn('performance: nomark');
  }
};

export const getMatrixTemplateDatas = ({ adMatrixInfo, styleInfoType }) => {
  const styleInfo = adMatrixInfo?.adDataV2?.[styleInfoType] || {};
  const { templateId = '' } = styleInfo;
  const templateDatas = adMatrixInfo?.adDataV2?.templateDatas || [];
  const matrixTemplateData =
    templateDatas?.find((templateData) => {
      return templateData?.templateId === templateId;
    }) || {};
  return matrixTemplateData;
};
// 判断是否为安卓4
export const isAndroid4 = ({ osType, systemVersion }) => {
  if (osType !== 2 && systemVersion.startsWith('4.')) {
    return true;
  }
  return false;
};
