import React, { FC, useState } from 'react';
import {
  AppIconSize,
  PageName,
  H5_DEFAULT_ICON_URL,
  DOWNLOAD_DEFAULT_ICON_URL,
  ItemClickType,
} from '@enum';
import './index.less';
import './wpx.less';
import './npx.less';
interface AppIconProps {
  appIconUrl: string;
  size?: AppIconSize;
  pageName?: PageName;
  itemclicktype?: ItemClickType;
  extraClass?: string;
  isDownload?: boolean;
  withWpx?: boolean;
  withNpx?: boolean;
}

const AppIcon: FC<AppIconProps> = ({
  appIconUrl,
  size = AppIconSize.NORMAL,
  pageName,
  itemclicktype,
  extraClass = '',
  withWpx = false,
  withNpx = false,
  isDownload = true,
}) => {
  return (
    <img
      itemclicktype={itemclicktype || ItemClickType.PLAY_END_ICON_CLICK}
      src={appIconUrl || (isDownload ? DOWNLOAD_DEFAULT_ICON_URL : H5_DEFAULT_ICON_URL)}
      className={`app-icon ${size} ${pageName} ${extraClass} ${withWpx ? 'with-wpx' : ''} ${
        withNpx ? 'with-npx' : ''
      }`}
    />
  );
};

export default AppIcon;
