import React, { FC } from 'react';
import { AdIconPositionName } from '@enum';
import { isAndroid4 } from '@util';

import './index.less';
import './wpx.less';
import './npx.less';
interface AdIconProps {
  positionName?: AdIconPositionName;
  info: any;
  withoutBg?: boolean;
  unit?: string;
  extraClass?: string;
}
const AdIcon: FC<AdIconProps> = ({
  positionName = AdIconPositionName.LEFT_BOTTOM,
  info,
  withoutBg = false,
  unit = '',
  extraClass = '',
}) => {
  // 素材外的logo都是灰色logo，素材里的logo灰底白色logo
  const iconUrl =
    info.adGrayMarkIcon || info.adMarkIcon
      ? withoutBg
        ? info.adGrayMarkIcon
        : info.adMarkIcon
      : '';
  const bg = withoutBg ? 'withoutBg' : '';
  const { osType, systemVersion } = info;
  // 安卓4不支持vw vmin
  unit = isAndroid4({ osType, systemVersion }) ? 'npx' : unit;
  const withUnit = unit ? `with-${unit}` : '';
  const backgroundImage = `url(${iconUrl})`;
  // 兜底方案
  if (!info.adSourceDescription && !iconUrl) {
    return (
      <div className={`ad-union-icon ${withUnit} ${positionName} ${bg} ${extraClass}`}>
        <div className="aglin">
          <div className={withoutBg ? 'iconGray' : 'iconWhite'} />
          <div className={withoutBg ? 'gray' : 'white'}>广告</div>
        </div>
      </div>
    );
  }

  return (
    <div className={`ad-union-icon ${withUnit} ${positionName} ${bg} ${extraClass}`}>
      <div className="aglin">
        {iconUrl && (
          <div
            style={{ backgroundImage }}
            className={info.adSourceDescription ? 'icon margin-right-4' : 'icon'}
          />
        )}
        <div className={withoutBg ? 'gray' : 'white'}> {info.adSourceDescription}</div>
      </div>
    </div>
  );
};

export default AdIcon;
