import React from 'react';
import { render } from 'react-dom';
import {
  PageStatus,
  AdOperationType,
  DEFAULT_APP_NAME,
  H5_DEFAULT_ICON_URL,
  DOWNLOAD_DEFAULT_ICON_URL,
  ScreenOrientation,
  SDKTypes,
  LIVE_DEFAULT_ICON_URL,
} from '@enum';
import { error, radarFmp, performanceCollect, radarInfo, radarEnd, radarStart } from '@log';
import { bridge } from '@bridge';
import {
  dpr,
  isIOS,
  initDevideInfo,
  performanceMark,
  getImgUrl,
  getIcon,
  jugeUrl,
  compareVersion,
} from '@util';
import {
  changePageStatus,
  getDeviceInfo,
  getKsAdExtraData as _getKsAdExtraData,
  getContainerPureLimit,
} from '@api';
import {
  AdInfo,
  RenderPageProps,
  GetInfoProps,
  InitShowProps,
  BtnTexts,
  DeviceInfo,
} from '@interface';

export const getInfo = ({
  styleInfoType,
  adBaseInfo,
  adInsertScreenInfo,
  adMaterialInfo,
  advertiserInfo,
  adConversionInfo,
  adStyleInfo,
  isPorttrait = true,
  containerWidth = 0,
  containerHeight = 0,
  adapterTopHeight = 0,
  deviceInfo,
  downloadSafeInfo,
  type,
  adMatrixInfo,
  materialFeature,
  adProductInfo = {},
  isGetEcInfo = false,
  ocpcActionType,
  adStyle,
}: GetInfoProps) => {
  const matrixData = getMatrixData({ adMatrixInfo, styleInfoType });
  const ecInfo = isGetEcInfo ? getEcInfo({ adProductInfo, matrixData }) : {};
  let adTitle = '';
  let adAppUrl = '';
  let ecPortraitUrl = '';
  const appName = adBaseInfo?.appName;
  const rawUserName = advertiserInfo?.rawUserName;
  const userName = advertiserInfo?.userName;
  const portraitUrl = advertiserInfo?.portraitUrl;
  const productName = adBaseInfo?.productName;
  const appIconUrl = adBaseInfo?.appIconUrl;
  const expParamString = adBaseInfo?.expParam;
  let expParam;
  try {
    expParam = JSON.parse(expParamString);
  } catch (e) {
    expParam = expParamString;
  }
  // 判断是下载类还是非下载类广告
  const isDownload = checkMatrixData(
    matrixData,
    'isDownload',
    Number(adBaseInfo?.adOperationType) === AdOperationType.DOWNLOAD
  );
  /**
   * adTitle 兜底策略
   * 下载类 appName > rawUserName > 可爱的广告君
   * 非下载类 productName > rawUserName > 可爱的广告君
   */

  if (isDownload) {
    adTitle = checkMatrixData(matrixData, 'adTitle', appName || rawUserName || DEFAULT_APP_NAME);
  } else {
    adTitle = checkMatrixData(
      matrixData,
      'adTitle',
      productName || rawUserName || DEFAULT_APP_NAME
    );
  }

  /**
   * adAppUrl 兜底策略
   * 下载类 appIconUrl > portraitUrl > 默认ICON
   * 非下载类 portraitUrl > 默认ICON
   */
  if (isDownload) {
    adAppUrl = checkMatrixData(
      matrixData,
      'adAppUrl',
      appIconUrl || portraitUrl || DOWNLOAD_DEFAULT_ICON_URL
    );
  } else {
    adAppUrl = checkMatrixData(matrixData, 'adAppUrl', portraitUrl || H5_DEFAULT_ICON_URL);
  }

  // 电商头像Url
  ecPortraitUrl = portraitUrl || H5_DEFAULT_ICON_URL;

  // 是否为视频素材
  const isVideo = Number(adMaterialInfo?.materialType) === 1;

  // 是否竖版素材
  const imageSize = adMaterialInfo?.materialFeature?.[0]?.materialSize;
  const isVerticalMaterial = checkMatrixData(
    matrixData,
    'isVerticalMaterial',
    imageSize?.width < imageSize?.height
  );

  // 判断屏幕是横竖
  const screenSub = isPorttrait ? 'SHU' : 'HENG';
  // 判断背景图的横竖
  const imageSub = isVerticalMaterial ? 'SHU' : 'HENG';
  // 获取版式打点
  const adStyleSub = `${screenSub}_SCREEN_${imageSub}_VERSION`;

  // adStyleInfo可能会是JSONString
  try {
    if (typeof adStyleInfo === 'string') {
      adStyleInfo = JSON.parse(adStyleInfo);
    }
  } catch (e) {
    adStyleInfo = adStyleInfo;
  }
  const typePortrait = adStyleInfo?.playEndInfo?.adWebCardInfo?.typePortrait;
  const typeLandscape = adStyleInfo?.playEndInfo?.adWebCardInfo?.typeLandscape;
  const typeLandscapePlayDetail = adStyleInfo?.playDetailInfo?.detailWebCardInfo?.typeLandscape;
  const typePortraitPlayDetail = adStyleInfo?.playDetailInfo?.detailWebCardInfo?.typePortrait;
  const exposeTagInfoList = checkMatrixData(
    matrixData,
    'exposeTagInfoList',
    adStyleInfo?.extraDisplayInfo?.exposeTagInfoList
  );
  const cardData = adStyleInfo?.playEndInfo?.adWebCardInfo?.cardData;
  const leftButtonText = cardData?.leftButtonText;
  const leftButtonTexts: BtnTexts = {
    adActionDescription: leftButtonText,
    installAppLabel: adBaseInfo?.installAppLabel || '立即安装',
    openAppLabel: adBaseInfo?.openAppLabel || '立即打开',
  };
  const texts: BtnTexts = checkMatrixData(matrixData, 'texts', {
    adActionDescription: adBaseInfo?.adActionDescription || (isDownload ? '立即下载' : '点我试玩'),
    installAppLabel: adBaseInfo?.installAppLabel || '立即安装',
    openAppLabel: adBaseInfo?.openAppLabel || '立即打开',
  });
  const appScore = checkMatrixData(matrixData, 'appScore', Number(adBaseInfo?.appScore) || 0);
  const appDownloadCountDesc = checkMatrixData(
    matrixData,
    'appDownloadCountDesc',
    adBaseInfo?.appDownloadCountDesc
  );
  const adActionBarColor = checkMatrixData(
    matrixData,
    'adActionBarColor',
    adBaseInfo?.adActionBarColor
  );
  const { SDKVersion, sdkType, SDKVersionCode, screenWidth, screenHeight, systemVersion } =
    deviceInfo;
  const playableUrl = jugeUrl(adConversionInfo?.playableUrl || '')
    ? adConversionInfo?.playableUrl
    : '';
  const playableOrientation = adConversionInfo?.playableStyleInfo?.playableOrientation || 0;
  const h5Type = adConversionInfo?.h5Type || 0;

  let appImageUrl = adBaseInfo?.appImageUrl;
  let appImageSize = adBaseInfo?.appImageSize;
  if (!Array.isArray(appImageUrl) || (Array.isArray(appImageUrl) && !appImageUrl.length)) {
    appImageUrl = [materialFeature?.coverUrl];
    appImageSize = materialFeature?.materialSize;
  }
  const adDescription = matrixData?.adDescription || adBaseInfo?.adDescription;
  let appDescription = adBaseInfo?.appDescription;
  appDescription = appDescription
    ? appDescription.replace(/\n/g, '<br/>')
    : adDescription
    ? adDescription
    : '';
  let packageSize = checkMatrixData(
    matrixData,
    'packageSize',
    adBaseInfo?.packageSize ? Number(adBaseInfo?.packageSize) : 0
  );
  if (isNaN(packageSize)) {
    packageSize = 0;
  }
  let permissionInfo = checkMatrixData(
    matrixData,
    'permissionInfo',
    downloadSafeInfo?.permissionInfo || '权限信息扫描中'
  );
  permissionInfo = permissionInfo && permissionInfo.replace(/\n/g, '<br/>');
  const apiExpParam = checkMatrixData(
    matrixData,
    'apiExpParam',
    adBaseInfo?.apiExpParam || {
      apiBreathLamp: 0,
      apiMisTouch: 0,
      apiAdTag: 0,
      tagTip: '即刻体验',
    }
  );
  const interactiveStyle = checkMatrixData(matrixData, 'interactiveStyle', null);
  const shakeInfo = checkMatrixData(matrixData, 'shakeInfo', {});

  const info: AdInfo = {
    // 广告信息
    adTitle,
    adAppUrl,
    adDescription: checkMatrixData(matrixData, 'adDescription', adBaseInfo?.adDescription),
    adActionDescription: texts.adActionDescription,
    adMarkIcon: adBaseInfo?.adMarkIcon,
    adGrayMarkIcon: adBaseInfo?.adGrayMarkIcon,
    adSourceDescription: adBaseInfo?.adSourceDescription,
    adSourceType: adBaseInfo?.adSourceType,
    adIconInfo: getIcon(adBaseInfo),
    leftButtonTexts,
    texts,
    ecPortraitUrl,
    // 商品信息
    goodsIcon: adProductInfo?.icon || H5_DEFAULT_ICON_URL,
    goodsName: adProductInfo?.name,
    goodsPrice: adProductInfo?.price,
    goodsOriginPrice: adProductInfo?.originPrice,
    goodsCoupon: adProductInfo?.coupon,

    // 物料信息
    materialUrl: adMaterialInfo?.materialFeature?.[0]?.materialUrl,
    coverUrl: adMaterialInfo?.materialFeature?.[0]?.coverUrl,
    videoDuration: adMaterialInfo?.materialFeature?.[0]?.videoDuration || 0,
    materialFeature: adMaterialInfo?.materialFeature,
    materialType: adMaterialInfo?.materialType,
    adActionBarColor:
      adActionBarColor.indexOf('#') === 0 ? adActionBarColor : `#${adActionBarColor}`,
    imgUrl: getImgUrl(adMaterialInfo),
    // app信息
    appName,
    appIconUrl,
    productName,
    appScore,
    appDownloadCountDesc,
    appVersion: checkMatrixData(matrixData, 'appVersion', adBaseInfo?.appVersion),
    packageSize,
    appImageUrl,
    appImageSize,
    appCategory: adBaseInfo?.appCategory,
    appDescription,
    // 广告主信息
    rawUserName,
    userName,
    portraitUrl,
    corporationName: checkMatrixData(matrixData, 'corporationName', adBaseInfo?.corporationName),
    isFollowed: advertiserInfo?.followed,
    fansCount: advertiserInfo?.fansCount,
    brief: advertiserInfo?.brief,

    // 状态
    isDownload,
    isVideo,
    isPorttrait,
    isVerticalMaterial,
    adStyleSub,
    isIOS,

    // 容器
    containerWidth,
    containerHeight,
    adapterTopHeight,

    // 激励视频任务类型
    taskType: adBaseInfo?.taskType,

    // 额外信息
    expParam,
    typePortrait,
    typeLandscape,
    typePortraitPlayDetail,
    typeLandscapePlayDetail,
    exposeTagInfoList: Array.isArray(exposeTagInfoList)
      ? exposeTagInfoList.filter((item) => item.text)
      : [],
    playDetailInfo: adStyleInfo?.playDetailInfo,
    type: checkMatrixData(matrixData, 'type', type),
    apiExpParam,
    // 样式信息
    adStyleInfo,
    cardType: adMatrixInfo?.adDataV2?.endCardInfo?.cardType,
    interactiveStyle,
    shakeInfo,
    adStyle,
    // SDK相关信息
    SDKVersion,
    SDKVersionCode,
    sdkType,
    screenWidth,
    screenHeight,
    systemVersion,

    // 试玩信息
    playableUrl,
    playableOrientation,
    playableStyle: matrixData?.playableStyle ?? expParam?.playableStyle,
    // 操作转化信息
    h5Type, // 落地页类型，0 默认， 1 下载类自制。 2 下载类广告主上传， 3 非下载类广告主上传

    // 下载相关信息
    downloadSafeInfo,
    titleBarTextSwitch: downloadSafeInfo?.complianceInfo?.titleBarTextSwitch || 0,
    appPermissionInfoUrl: downloadSafeInfo?.appPermissionInfoUrl || '',
    appPrivacyUrl: checkMatrixData(
      matrixData,
      'appPrivacyUrl',
      downloadSafeInfo?.appPrivacyUrl || ''
    ),
    permissionInfo,

    // ID相关
    creativeId: adBaseInfo?.creativeId,
    // 自动关闭时间
    autoCloseTime: adInsertScreenInfo?.autoCloseTime || 0,

    // matrix信息
    adMatrixInfo,
    matrixData,

    // 电商信息
    ...ecInfo,
    /**
     * 电商直播相关信息
     */
    userHeadUrl: matrixData?.userHeadUrl || LIVE_DEFAULT_ICON_URL, // 主播头像
    bookUserCount: matrixData?.bookUserCount, // 预约人数
    liveTitle: matrixData?.title, // 直播标题
    liveStartTime: matrixData?.liveStartTime, // 开播时间
    displayBookCount: matrixData?.displayBookCount, // 是否展示预约人数
    bookUserUrlList: matrixData?.bookUserUrlList, // 预约头像
    displayWeakCard: matrixData?.displayWeakCard, // 是否展示弱卡
    // 广告类型
    ocpcActionType,
  };
  return info;
};

/**
 * 获取config接口数据
 */
export const getConfigData = async (
  {
    isGetConfig,
    isChangePageStatusWhenFail = true,
    adConfigMinSDKVersion,
    contentConfigMinSDKVersion,
  }: {
    isGetConfig: boolean;
    isChangePageStatusWhenFail: boolean;
    adConfigMinSDKVersion: string;
    contentConfigMinSDKVersion: string;
  },
  deviceInfo
) => {
  let getKsAdConfigData: any = {};
  /**
   * 兼容低版本安卓没有 config 桥
   * 内容版本暂时不会更新，比当前版本27大一个版本即可
   */
  const isGreaterrVersion =
    (adConfigMinSDKVersion &&
      compareVersion(deviceInfo?.SDKVersion, adConfigMinSDKVersion) >= 0 &&
      deviceInfo?.sdkType === SDKTypes.AD) ||
    (contentConfigMinSDKVersion &&
      compareVersion(deviceInfo?.SDKVersion, contentConfigMinSDKVersion) >= 0 &&
      deviceInfo?.sdkType === SDKTypes.CONTENT);

  if (isGetConfig || isGreaterrVersion) {
    radarStart('getKsAdConfigFailRate');
    try {
      getKsAdConfigData = await bridge.getKsAdConfig();
    } catch (e: any) {
      if (isChangePageStatusWhenFail) {
        radarEnd('getKsAdConfigFailRate');
        error('getKsAdConfig fail', {
          isBridgeError: true,
        });
        changePageStatus(PageStatus.ERROR);
      }
      return {};
    }
  }
  return getKsAdConfigData.data;
};

/**
 * 获取容器宽高，判断横竖屏
 */

export const getLimitInfo = async (isGetContainerInfo: boolean, isPureContainer?: boolean) => {
  let limit = null;
  let containerWidth: number = 0;
  let containerHeight: number = 0;
  let adapterTopHeight: number = 0;
  if (isGetContainerInfo) {
    try {
      const getContainerLimit = isPureContainer ? getContainerPureLimit : bridge.getContainerLimit;
      limit = await getContainerLimit();
      const containerLimit = limit && limit.data;
      containerHeight = containerLimit.height / dpr;
      containerWidth = containerLimit.width / dpr;
      if (isPureContainer) {
        adapterTopHeight = containerLimit?.adapterTopHeight / dpr || 0;
      }
    } catch (e: any) {
      error(`${isPureContainer ? 'getContainerPureLimit' : 'getContainerLimit'} fail`, {
        isBridgeError: true,
      });
      changePageStatus(PageStatus.ERROR);
      return {
        containerHeight,
        containerWidth,
        adapterTopHeight,
      };
    }
  }
  return {
    containerHeight,
    containerWidth,
    adapterTopHeight,
  };
};

/**
 * 获取屏幕方向，判断横竖屏
 */
export const getIsPorttrait = async () => {
  let isPorttrait = true;
  try {
    const screenOrientationRes = await bridge.getScreenOrientation();
    isPorttrait = screenOrientationRes.data.screenOrientation === ScreenOrientation.PORTTRAIT;
  } catch (e) {
    error('getScreenOrientation fail', {
      isBridgeError: true,
    });
    changePageStatus(PageStatus.ERROR);
    return isPorttrait;
  }
  return isPorttrait;
};

/**
 * webview可见时
 */
export const initShow = ({ show, info, infoList, deviceInfo, extraData }: InitShowProps) => {
  if (show) {
    bridge.registerLifecycleListener({
      success(res) {
        const { lifeStatus } = res.data;
        if (lifeStatus === 'showEnd') {
          show({ isIOS, info, infoList, deviceInfo, extraData });
        }
      },
    });
  }
};

/**
 * 获取SDK相关信息
 */
export const getDeviceSDKInfo = async ({
  isChangePageStatusWhenFail = true,
}: {
  isChangePageStatusWhenFail: boolean;
}) => {
  let deviceInfo: DeviceInfo = initDevideInfo({});
  try {
    deviceInfo = await getDeviceInfo();
  } catch (e) {
    if (isChangePageStatusWhenFail) {
      error('getDeviceInfo failed');
    }
    return deviceInfo;
  }
  return deviceInfo;
};

/**
 * 渲染组件
 */
export const renderPage = ({
  component,
  renderSuccess,
  info,
  infoList,
  adData,
  configData,
  root,
  impAdInfo,
  extraData,
}: RenderPageProps) => {
  const Page = component;
  try {
    // TODO 获取额外信息的桥，做开关，更细粒度的控制
    // 最新的js代码，跑在老的sdk里，如何做兼容
    const pageResult = (
      <Page
        info={info}
        infoList={infoList}
        adData={adData}
        configData={configData}
        impAdInfo={impAdInfo}
        extraData={extraData}
      />
    );
    render(pageResult, root, () => {
      // 上报fmp
      radarFmp();
      if (renderSuccess) {
        renderSuccess({ info, infoList, configData }); // 渲染成功时的回调
      } else {
        changePageStatus(PageStatus.RENDER_COMPLETE);
      }
      radarInfo('impression');
      const t_reder = performance.now();
      if (t_reder < 1500) {
        radarInfo('impression_success');
        radarStart('impressionConvertRate');
        radarEnd('impressionRate');
      } else {
        radarInfo('impression_error');
      }
      performanceMark('renderEnd');
      performanceCollect();
    });
  } catch (e: any) {
    console.error(e);
    error('render fail: ', { e });
    changePageStatus(PageStatus.ERROR);
    return;
  }
};
export const getMatrixData = ({ adMatrixInfo, styleInfoType }) => {
  const styleInfo = adMatrixInfo?.adDataV2?.[styleInfoType] || {};
  const { templateId = '' } = styleInfo;
  const templateDatas = adMatrixInfo?.adDataV2?.templateDatas || [];
  const matrixDataString =
    templateDatas?.find((templateData) => {
      return templateData?.templateId === templateId;
    })?.data || '{}';
  let matrixData;
  try {
    matrixData = JSON.parse(matrixDataString);
  } catch (e) {
    matrixData = {};
  }
  return matrixData;
};

export const getKsAdExtraData = async (isGetKsAdExtraData: boolean) => {
  if (!isGetKsAdExtraData) return {};
  const ksAdExtraData = await _getKsAdExtraData();
  return ksAdExtraData.data;
};
export const checkMatrixData = (matrixData, k, base) => {
  return typeof matrixData?.[k] !== 'undefined' && matrixData?.[k] !== null
    ? matrixData?.[k]
    : base;
};
export const getEcInfo = ({ adProductInfo, matrixData }) => {
  let result: any = {};
  result.ecTitle = checkMatrixData(matrixData, 'name', adProductInfo?.name);
  result.ecIcon = checkMatrixData(matrixData, 'icon', adProductInfo?.icon);
  result.originPrice = checkMatrixData(matrixData, 'originPrice', adProductInfo?.originPrice);
  result.price = checkMatrixData(matrixData, 'price', adProductInfo?.price);
  result.volume = checkMatrixData(matrixData, 'volume', adProductInfo?.volume);
  result.rating = checkMatrixData(matrixData, 'rating', adProductInfo?.rating);
  result.couponList = checkMatrixData(matrixData, 'couponList', adProductInfo?.couponList);
  const timeList = ['startFetchTime', 'endFetchTime', 'leftFetchTime'];
  // 优惠券只会返回一张最优的，所以可以直接取数组的第一项
  const couponListInfo = result.couponList?.[0];
  for (let k in couponListInfo) {
    if (timeList.indexOf(k) > -1) {
      couponListInfo[k] = Number(couponListInfo[k]);
    }
  }
  result = { ...result, ...result.couponList?.[0] };
  return result;
};
