import React from 'react';
import Draw from './page/draw';

import { PageStatus } from '@enum';
import { changePageStatus, initKsAdFrame } from '@api';
import { error, radarEnd, radarStart } from '@/common/log';
import { CardStyleType } from './interfaces';
import { querySize, initViewportSize } from '@util';

const PageWrapper = ({ info }) => {
  const { typePortraitPlayDetail, screenWidth, screenHeight, isDownload } = info;
  const client = { screenWidth, screenHeight };

  function getCurrentPage() {
    radarStart('typePortraitErrorRate');
    switch (typePortraitPlayDetail) {
      case CardStyleType.STYLE_3:
        return <Draw info={info} isDownload={isDownload} />;
      default: {
        radarEnd('typePortraitErrorRate');
        error('typePortrait error', {
          isBridgeError: true,
        });
        changePageStatus(PageStatus.ERROR);
        return;
      }
    }
  }

  const page = getCurrentPage();
  if (!page) {
    return;
  }
  initViewportSize(client, querySize(3), false);
  return page;
};

export default PageWrapper;
