import { AdInfo } from '@interface';
import { init, getListData, changePageStatus } from '@api';
import { PageStatus } from '@enum';
import { styleInfoMap } from '@/common/config';
import { performanceMark } from '@util';
import {
  getInfo,
  initShow,
  getIsPorttrait,
  getLimitInfo,
  getConfigData,
  renderPage,
  getDeviceSDKInfo,
  getKsAdExtraData,
} from './util';
import { radarInfo, radarStart, radarEnd } from '@log';
const NormalAd = async ({
  name,
  show,
  component,
  renderSuccess,
  root = document.getElementById('app'),
  adType,
  isGetConfig,
  isGetContainerInfo,
  isGetKsAdExtraData,
  isChangePageStatusWhenFail = true,
  isGetEcInfo,
  isPureContainer = false,
  adConfigMinSDKVersion,
  contentConfigMinSDKVersion,
}) => {
  radarInfo('preload');
  radarStart('impressionRate');
  performanceMark('initBridgeStart');
  init({ cardType: name });
  performanceMark('fetchBridgeStart');
  const styleInfoType = styleInfoMap[name];

  const { containerHeight, containerWidth, adapterTopHeight } = await getLimitInfo(
    isGetContainerInfo,
    isPureContainer
  );

  const [deviceInfo, isPorttrait, extraData] = await Promise.all([
    getDeviceSDKInfo({ isChangePageStatusWhenFail }),
    getIsPorttrait(),
    getKsAdExtraData(isGetKsAdExtraData),
  ]);

  const configData = await getConfigData(
    { isGetConfig, isChangePageStatusWhenFail, adConfigMinSDKVersion, contentConfigMinSDKVersion },
    deviceInfo
  );

  getListData(name)
    .then(({ adInfo, impAdInfo }) => {
      const infoList: AdInfo[] = adInfo.map((item) => {
        const {
          adBaseInfo,
          adInsertScreenInfo = {},
          adMaterialInfo,
          advertiserInfo,
          adStyleInfo,
          adConversionInfo,
          downloadSafeInfo,
          type,
          adMatrixInfo = {},
          materialFeature,
          ocpcActionType,
          adProductInfo = {},
          adStyle,
        } = item;
        const info: AdInfo = getInfo({
          styleInfoType,
          adBaseInfo,
          adInsertScreenInfo,
          adMaterialInfo,
          advertiserInfo,
          adStyleInfo,
          isPorttrait,
          containerHeight,
          containerWidth,
          adConversionInfo,
          deviceInfo,
          downloadSafeInfo,
          type,
          adMatrixInfo,
          materialFeature,
          adProductInfo,
          isGetEcInfo,
          ocpcActionType,
          adapterTopHeight,
          adStyle,
        });
        return info;
      });
      /* tslint:disable-next-line */
      console.log(adType, adType === 'multi' ? infoList : infoList[0], 'info=============');
      performanceMark('fetchBridgeEnd');
      initShow({
        show,
        info: infoList[0],
        infoList,
        deviceInfo,
        extraData,
      });

      renderPage({
        component,
        renderSuccess,
        info: infoList[0],
        infoList,
        adData: adInfo,
        configData,
        root,
        adType,
        impAdInfo,
        extraData,
      });
    })
    .catch((e) => {
      changePageStatus(PageStatus.ERROR);
    });
};

export default NormalAd;
