export const clickAction = ({
  unlog,
  area = 1,
  e,
  payload = '{}',
  adTemplate,
  refreshType,
  creativeId,
  callback,
  needReport = true,
  containerWidth = window.screen.width,
  containerHeight = window.screen.height,
  actionType = AdActionType.AD_ITEM_CLICK,
  adStyle = 0,
}: ClickActionProps) => {
  radarStart('convertRate');
  radarEnd('impressionConvertRate');
  radarInfo('convert');
  if (unlog === 'true') {
    return;
  }
  const clickActionButton = area === 1;
  const clickInfo = {
    x: e?.pageX,
    y: e?.pageY,
    width: containerWidth,
    height: containerHeight,
  };
  let _payload: string = '{}';
  if (typeof payload === 'string') {
    _payload = payload;
  } else {
    _payload = JSON.stringify(payload);
  }

  const logParam = {
    actionType,
    clickInfo,
    payload: _payload,
    refreshType,
  };
  const clickActionProps: BridgeClickActionProps = {
    area,
    logParam,
    needReport,
  };
  if (adTemplate) {
    clickActionProps.adTemplate = adTemplate;
  }
  if (creativeId) {
    clickActionProps.creativeId = creativeId;
  }
  if (adStyle) {
    clickActionProps.adStyle = adStyle;
  }
  bridge
    .clickAction(clickActionProps)
    .then(() => {
      callback && callback();
      radarEnd('convertRate');
      radarInfo('convert_success');
    })
    .catch((e) => {
      const logAndConvertProps: LogAndConvertProps = {
        clickInfo,
        payload: _payload,
        clickActionButton,
        callback,
        actionType,
      };
      if (adTemplate) {
        logAndConvertProps.adTemplate = adTemplate;
      }
      if (refreshType) {
        logAndConvertProps.refreshType = refreshType;
      }
      logAndConvert(logAndConvertProps);
      radarInfo('convert_error');
    });
};

export const logAndConvert = ({
  clickInfo,
  payload,
  clickActionButton,
  adTemplate,
  refreshType,
  callback,
  actionType = AdActionType.AD_ITEM_CLICK,
}: LogAndConvertProps) => {
  track({
    actionType,
    clickInfo,
    payload,
    adTemplate,
    refreshType,
  });
  convert({
    clickActionButton,
    adTemplate,
  });
  callback && callback();
};

