// 做一层代理，因为注册多次监听，只会触发一次，所以我们这里就将这些回调维护成一个数组
const fnList: RegisterProgressListenerProps[] = [];
const isHasRegister = false;
export const registerProgressListener = ({
  creativeId,
  success,
  isMulti = false,
}: {
  creativeId?: number;
  success: (param: any) => void;
  isMulti?: boolean;
}) => {
  fnList.push({
    creativeId,
    success,
  });
  if (!isHasRegister) {
    if (isMulti) {
      bridge.registerMultiProgressListener({
        creativeId,
        success(res) {
          fnList.forEach(({ creativeId, success }) => {
            if (res?.data?.creativeId === creativeId) {
              return success(res);
            }
          });
        },
      });
    } else {
      bridge.registerProgressListener({
        success(res) {
          fnList.forEach(({ success }) => success(res));
        },
      });
    }
  }
};

// 是否支持滑动点击
export const sildeEventListener = ({
  el = document.querySelector('body'),
  slideClick = false,
  cb = (e, payload?: {}): void => {},
  minLength = 1,
  forTappableEl = false,
}) => {
  let touchStartInfo: any;
  const pos = {
    x: 0,
    y: 0,
  };
  let _cb = cb;
  if (forTappableEl) {
    _cb = (e: any, payload: { [key: string]: any } = {}) => {
      let target = (e.target || e.currentTarget) as HTMLElement;

      let itemClickType;
      try {
        while (target) {
          if (target.getAttribute) {
            itemClickType = target.getAttribute('itemclicktype');
          }
          if (itemClickType) {
            target = null;
          } else if (target.parentNode) {
            target = target.parentNode as HTMLElement;
          } else {
            target = null;
          }
        }
      } catch (err) {
        error('click parse target error:', { e: err });
      }
      if (itemClickType) {
        payload.item_click_type = itemClickType;
      }
      cb(e, payload);
    };
  }

  function onTouchStart(e) {
    e.stopPropagation();
    pos.x = e.changedTouches[0].pageX;
    pos.y = e.changedTouches[0].pageY;
    touchStartInfo = e.changedTouches[0];
  }

  function onTouchEnd(e) {
    e.stopPropagation();
    const deltaX = Math.abs(e.changedTouches[0].pageX - pos.x);
    const deltaY = Math.abs(e.changedTouches[0].pageY - pos.y);
    e.touchStartInfo = touchStartInfo;
    if (deltaX > minLength || deltaY > minLength) {
      // 滑动触发回调
      console.warn('滑动点击');
      let payload = { item_click_type: ItemClickType.CAED_SKIDE_CLICK };
      slideClick && _cb(e, payload);
    } else {
      console.warn('点击事件');
      _cb(e);
    }
  }
  if (slideClick === false) {
    // 默认走click点击
    el.addEventListener('click', _cb, false);
  } else {
    el.addEventListener('touchstart', onTouchStart, false);
    el.addEventListener('touchend', onTouchEnd, false);
  }

  return () => {
    // 增加去除监听函数
    if (slideClick === false) {
      // 默认走click点击
      el.removeEventListener('click', _cb, false);
    } else {
      el.removeEventListener('touchstart', onTouchStart, false);
      el.removeEventListener('touchend', onTouchEnd, false);
    }
  };
};
export const initEventListener = (adStyleSub, info: any = {}) => {
  // 是否支持滑动点击
  const slideClick = info?.adStyleInfo?.slideClick;

  const cb = (e: any, payloadParam) => {
    let type;
    let target = (e.target || e.currentTarget) as HTMLElement;
    let itemClickType;
    let buttonDetail = '';
    let unConvert;
    let unlog;
    try {
      while (target) {
        if (target.getAttribute) {
          itemClickType = target.getAttribute('itemclicktype');
          buttonDetail = target.getAttribute('buttondetail');
          unConvert = target.getAttribute('unconvert');
          unlog = target.getAttribute('unlog');
        }
        if (itemClickType) {
          type = itemClickType;
          target = null;
        } else if (target.parentNode) {
          target = target.parentNode as HTMLElement;
        } else {
          target = null;
        }
      }
    } catch (err) {
      error('click parse target error:', { e: err });
    }
    const clickActionButton = buttonDetail !== null;
    const area = clickActionButton ? 1 : 2;
    if (slideClick && payloadParam?.item_click_type) {
      itemClickType = payloadParam.item_click_type;
    } // 支持滑动点击上报具体值

    const payload = JSON.stringify({
      item_click_type: Number(itemClickType) || ItemClickType.BLANK_AREA_CLICK,
      button_detail: buttonDetail || '', // 如果没有的话，iOS需要传空字符串
      ad_style_sub: adStyleSub,
    });
    if (!unlog) {
      clickAction({
        e,
        payload,
        area,
      });
    }
  };

  sildeEventListener({ cb, slideClick });
};

export const initPlayDetailEventListener = (info: any = {}) => {
  // 是否支持滑动点击
  const slideClick = info?.adStyleInfo?.slideClick;

  const cb = (e: any, payloadParam) => {
    let type;
    let target = (e.target || e.currentTarget) as HTMLElement;
    let itemClickType;
    let actionType;
    let unConvert;
    let unlog;
    try {
      while (target) {
        if (target.getAttribute) {
          itemClickType = target.getAttribute('itemclicktype');
          actionType = target.getAttribute('actiontype');
          unConvert = target.getAttribute('unconvert');
          unlog = target.getAttribute('unlog');
        }
        if (itemClickType || actionType) {
          type = itemClickType;
          target = null;
        } else if (target.parentNode) {
          target = target.parentNode as HTMLElement;
        } else {
          target = null;
        }
      }
    } catch (err) {
      error('click parse target error:', { err });
    }
    itemClickType = Number(itemClickType) || ItemClickType.BLANK_AREA_CLICK;
    const clickActionButton =
      itemClickType === ItemClickType.CARD_ACTION_BAR_CLICK ||
      itemClickType === ItemClickType.PLAYEND_ACTION_BAR_CLICK;
    // 支持滑动点击上报具体值
    if (slideClick && payloadParam?.item_click_type) {
      itemClickType = payloadParam.item_click_type;
    }

    if (unlog) {
      // unlog的时候不打点
      return;
    } else if (!unConvert || clickActionButton) {
      const area = clickActionButton ? 1 : 2;
      clickAction({
        e,
        payload: JSON.stringify({
          itemClickType,
        }),
        area,
      });
    } else {
      // 关闭打点没有自定义参数且不执行convert
      if (itemClickType) {
        // 约定：itemClickType只能是数字或{itemCloseType:xxx}的对象字符串两种类型
        try {
          if (typeof JSON.parse(itemClickType) === 'object') {
            // 快享有自定义参数
            // 快享的关闭按钮已经预先把itemClickType转化为了对象字符串
            track({
              actionType: Number(actionType) || AdActionType.AD_ITEM_CLICK,
              payload: itemClickType,
              clickInfo: {
                x: e.pageX,
                y: e.pageY,
              },
            });
          } else {
            // 其他场景，itemClickType是数字需要在adSimplelog方法内部转化为字符串
            track({
              actionType: Number(actionType) || AdActionType.AD_ITEM_CLICK,
              payload: JSON.stringify({
                itemClickType: Number(itemClickType) || ItemClickType.BLANK_AREA_CLICK,
              }),
              clickInfo: {
                x: e.pageX,
                y: e.pageY,
              },
            });
          }
        } catch (e: any) {
          // 其他场景，itemClickType是数字需要在adSimplelog方法内部转化为字符串
          track({
            actionType: Number(actionType) || AdActionType.AD_ITEM_CLICK,
            payload: JSON.stringify({
              itemClickType: Number(itemClickType) || ItemClickType.BLANK_AREA_CLICK,
            }),
            clickInfo: {
              x: e.pageX,
              y: e.pageY,
            },
          });
        }
      } else {
        track({
          actionType: Number(actionType) || AdActionType.AD_ITEM_CLICK,
          payload: '{}', // 没有自定义参数
          clickInfo: {
            x: e.pageX,
            y: e.pageY,
          },
        });
      }
    }
  };

  sildeEventListener({ cb, slideClick });
};
// 单例 模式视频播放进度
let registerVideoProgressSing = null;
let registerVideoProgressListenerList = [];

export const registerVideoProgress = (opt: any = {}) => {
  const { success } = opt;
  registerVideoProgressListenerList.push(success);

  if (registerVideoProgressSing === null) {
    registerVideoProgressSing = bridge.registerVideoProgressListener({
      success(res) {
        registerVideoProgressListenerList.forEach((suc) => {
          suc(res); // 成功回调
        });
      },
    });
  }
  return registerVideoProgressSing;
};

