/**
 * 设置局部动态卡宽高 及 位置
 * 目前为测试横屏状态，待遇到需求再完善
 */
export const initWebviewOffset = ({
  designWidth,
  designHeight,
  designDeviceWidth,
  designBottomMargin = 0,
  designLeftMargin = 0,
}) => {
  return Promise.all([bridge.getContainerLimit(), bridge.getDeviceInfo()]).then(
    ([limit, deviceInfo]) => {
      const webviewWidth = limit.data.width;
      const deviceWidth = deviceInfo.data.screenWidth;
      let newWebviewWidth = (designWidth / designDeviceWidth) * deviceWidth;
      const leftMargin = (deviceWidth / designDeviceWidth) * designLeftMargin;
      const bottomMargin = (deviceWidth / designDeviceWidth) * designBottomMargin;
      let rightMargin = webviewWidth - newWebviewWidth - leftMargin;
      if (rightMargin < 0) {
        // 如果计算出来的rightMargin 小于0，则说明我们按比例缩放计算出来的webview的宽度 大于 客户端提供的宽度
        // 此时直接取客户端提供的宽度即可。
        newWebviewWidth = webviewWidth;
        rightMargin = 0;
      }
      bridge.initKsAdFrame({
        height: Math.round(designHeight * newWebviewWidth) / designWidth,
        leftMargin: Math.round(leftMargin),
        rightMargin: Math.round(rightMargin),
        bottomMargin: Math.round(bottomMargin),
      });
    }
  );
};

