/**
 * 获取图文信息
 * 和getData的区别是不需要scheme，不需要adInfo
 */
export const getImgTextData = async (mockFileName?: string): Promise<any> => {
  let getDataRes;
  try {
    // @ts-ignore
    getDataRes = await bridge.getKsAdData(mockFileName);
  } catch (e) {
    error('getKsAdData fail');
    changePageStatus(PageStatus.ERROR);
    return;
  }
  const impAdInfo = getDataRes?.data?.impAdInfo || [];
  try {
    impAdInfo.forEach((info) => {
      setWebloggerExtra({
        creativeId: info.adInfo?.[0]?.adBaseInfo?.createiveId,
        posId: info.posId || 0,
      });
    });
  } catch (e) {
    error('Set Weblogger OncallExtra Failed: ', { e, getDataRes });
    changePageStatus(PageStatus.ERROR);
    return;
  }
  const newsInfo = getDataRes?.data?.impAdInfo?.[0]?.newsInfo;
  if (!newsInfo) {
    error('no newsInfo Data: ', { getDataRes });
    changePageStatus(PageStatus.ERROR);
    return Promise.reject('no newsInfo Data');
  }
  return newsInfo;
};

