import React, { FC } from 'react';
import AppIcon from '@/components/AppIcon';
import Button from '@/components/Button';
import AdUnionIcon from '@/components/AdUnionIcon/index';
import { ItemClickType, AdActionType, AppIconSize, AdIconPositionName, PageName } from '@enum';
import { bridge } from '@bridge';
import { AdStyleInfo } from './../../interfaces';
import { AdInfo } from '@/common/interface';
import './index.less';

interface CardProps {
    info: AdInfo;
    isDownload: boolean;
    pageName: string;
    adStyleInfo?: AdStyleInfo;
}

const FloatCard: FC<CardProps> = ({ info, isDownload, pageName }) => {
    const title = info.adTitle;
    const appIconSize = AppIconSize.SMALLER;
    // 下载类，需要有默认app icon
    const appIconUrl = info.adAppUrl;
    return (
        <div className={`float-card-v1 ${!isDownload && 'notDownload'} ${pageName}`}>
            <div className="top-contain">
                {appIconUrl && (
                    <AppIcon
                        pageName={pageName as PageName}
                        appIconUrl={appIconUrl}
                        size={appIconSize}
                        itemclicktype={ItemClickType.CARD_ICON_CLICK}
                        isDownload={info.isDownload}
                        extraClass="draw-app-icon"
                    />
                )}
                <div className="card-intro">
                    <div className="title-contain">
                        <h1 itemclicktype={ItemClickType.CARD_ICON_NAME_CLICK}>{title}</h1>
                        <AdUnionIcon
                            positionName={AdIconPositionName.TITLE_RIGHT}
                            info={info}
                            withoutBg={true}
                            unit="wpx"
                        />
                    </div>
                    {info.adDescription && (
                        <div
                            className="app-desc"
                            itemclicktype={ItemClickType.CARD_ICON_COMMENT_CLICK}
                        >
                            {info.adDescription}
                        </div>
                    )}
                </div>
            </div>

            <Button
                extraClass={'draw-button'}
                texts={info.texts}
                backgroundColor={'#0040ca'}
                isDownload={isDownload}
                itemclicktype={ItemClickType.CARD_ACTION_BAR_CLICK}
            />
            <div
                onClick={() => bridge.hide()}
                unconvert={'true'}
                actiontype={AdActionType.AD_CARD_CLOSE}
                className="close-btn"
            />
        </div>
    );
};

export default FloatCard;
