import Page from '@page';
import { track } from '@log';
import { AdActionType, PageStatus } from '@enum';
import { changePageStatus, initPlayDetailEventListener } from '@api';
import Card from './PageWrapper';
Page({
  name: 'ad-union-video-draw',
  component: Card,
  isGetContainerInfo: true,
  show() {
    track({
      actionType: AdActionType.AD_CARD_IMPRESSION,
    });
  },
  renderSuccess: async ({ info }) => {
    initPlayDetailEventListener(info);
    changePageStatus(PageStatus.RENDER_COMPLETE);
  },
});
