import { getDeviceInfo } from '@api';
import { SDKTypes } from '@enum';
import { compareVersion } from '@util';
const isProd = process.env.NODE_ENV === 'production';
const isJinx = process.env.PUBLIC_PATH === 'jinx';
const isDevAndTest = !isProd || isJinx;
interface ErrorArgs {
  isBridgeError?: boolean;
  e?: any;
  [key: string]: any;
}
export function error(message: string, args?: ErrorArgs) {
  if (isProd) {
    captureWebloggerException('card_error', message, args);
  } else {
    if (args) {
      // tslint:disable-next-line:no-console
      console.error('error:' + message, JSON.stringify(args));
    } else {
      // tslint:disable-next-line:no-console
      console.error('error:' + message);
    }
  }
}

export function radarInfo(
  message: string,
  args?: { [key: string]: any },
  event_type: string = 'card_info'
) {
  if (isProd) {
    try {
      window.weblog.plugins.radar.event({
        name: message,
        message: args,
        event_type,
        src: window.location.href,
        extra_info: window.weblog.oncallExtra,
      });
    } catch (e) {
      console.error('radar event error', e);
    }
  } else {
    if (args) {
      // tslint:disable-next-line:no-console
      console.info('info:' + message, JSON.stringify(args));
    } else {
      // tslint:disable-next-line:no-console
      console.info('info:' + message);
    }
  }
}

export function radarStart(name: string, extra_info?: string) {
  if (isProd) {
    try {
      window.weblog.plugins.radar.action.start({
        name,
        extra_info,
      });
    } catch (e) {
      console.error('radar start error', e);
    }
  } else {
    console.log(name, extra_info);
  }
}

export function radarEnd(name: string, extra_info?: string, result_type?: string) {
  if (isProd) {
    try {
      window.weblog.plugins.radar.action.end({
        name,
        extra_info, //【可选】
        result_type, //【可选】 事件结果
      });
    } catch (e) {
      console.error('radar end error', e);
    }
  } else {
    console.log(name, extra_info, result_type);
  }
}

export function radarCollect(name: string, duration: number) {
  if (isDevAndTest) {
    if (!window.collectInfo) window.collectInfo = {};
    window.collectInfo[name] = duration;
  }
  if (isProd) {
    try {
      window.weblog.plugins.radar.event(
        {
          name,
        },
        {
          duration,
        }
      );
    } catch (e) {
      console.error('radar collect error', e);
    }
  } else {
    console.log({ name, duration });
  }
}

// 过滤oncall系统上报所需字段
const getOncallExtra = async () => {
  const result = {
    deviceId: '',
    appVersion: '3.0.0',
    appName: '',
    appId: '',
    sdkVersion: '',
    sdkType: 0,
    sdkTypeDesc: '',
  };
  try {
    const data = await getDeviceInfo();
    let sdkType = null;
    if (data.sdkType === SDKTypes.AD) {
      sdkType = '广告';
    } else if (data.sdkType === SDKTypes.CONTENT) {
      sdkType = '内容';
    } else if (data.sdkType === SDKTypes.EC) {
      sdkType = '电商';
    } else if (data.sdkType === SDKTypes.CONTENT_PURE) {
      sdkType = '纯内容';
    } else if (data.sdkType === SDKTypes.CAR) {
      sdkType = '车机';
    } else {
      sdkType = '其他广告类型';
    }

    result.deviceId = data.uuid;
    result.appVersion = data.appVersion;
    result.appName = data.appName;
    result.appId = data.appId;
    result.sdkVersion = data.SDKVersion;
    result.sdkType = data.sdkType;
    result.sdkTypeDesc = sdkType;
  } catch (e) {
    error('getDeviceInfo fail');
  }
  return result;
};

export const radarFmp = () => {
  if (isProd) {
    try {
      window.weblog.plugins.radar.fmp();
    } catch (e) {
      console.error('radar fmp error', e);
    }
  }
};

export async function initWebloggerExtra() {
  if (isProd) {
    try {
      const oncallExtra = await getOncallExtra();
      window.weblog.plugins.radar.setDimensions({
        c_dimension1: oncallExtra.sdkVersion,
        c_dimension2: oncallExtra.appName,
        c_dimension3: oncallExtra.appId,
      });
      window.weblog.oncallExtra = JSON.stringify([oncallExtra]);
      return oncallExtra;
    } catch (e) {
      error('Init Weblogger OncallExtra Failed');
    }
  }
}

export function setWebloggerExtra(param, index = 0) {
  if (isProd) {
    try {
      const oncallExtraGroup = JSON.parse(window.weblog.oncallExtra);
      const len = oncallExtraGroup.length;
      if (index > 0) {
        oncallExtraGroup.push(oncallExtraGroup[len - 1]);
        oncallExtraGroup[len - 1] = { ...oncallExtraGroup[len - 1], ...param };
      } else {
        oncallExtraGroup[0] = { ...oncallExtraGroup[0], ...param };
      }
      window.weblog.oncallExtra = JSON.stringify(oncallExtraGroup);
    } catch (e) {
      error('Set Weblogger OncallExtra Failed');
    }
  }
}

/**
 * 获取 url 的参数对象
 * 通过 url 的 env 字段做开关，判断 error log 的内容
 */
const urlParamsMap = (function getUrlParams() {
  const p = window.location.search.slice(1),
    paramsMap = {};
  if (!p) return paramsMap;
  const params = p?.split('&');
  params.forEach((item) => {
    const [k, v] = item.split('=');
    paramsMap[k] = v;
  });
  return paramsMap;
})();
export const captureWebloggerException = (type: string, msg: string, args?: ErrorArgs) => {
  let sdkType;
  let sdkVersion;
  try {
    const oncallExtra = JSON.parse(window.weblog.oncallExtra);
    sdkType = oncallExtra?.[0]?.sdkType;
    sdkVersion = oncallExtra?.[0]?.sdkVersion;
  } catch (e) {
    return;
  }
  // 获取不到sdk版本时，不上报
  if (!sdkVersion || sdkVersion === '0.0.0') {
    return;
  }
  // 只跟踪最近几个版本的SDK异常，两个版本手动更新一次
  const isLessThanReportVersion =
    (compareVersion(sdkVersion, window?.radarReportSDKVersionAd || '3.3.22') < 0 &&
      sdkType === SDKTypes.AD) ||
    (compareVersion(sdkVersion, window?.radarReportSDKVersionContent || '3.3.27') < 0 &&
      sdkType === SDKTypes.CONTENT);

  // 桥报错，并且SDK版本过低的时候，不进行上报
  if (isLessThanReportVersion && args?.isBridgeError) {
    return;
  }

  let message = msg;
  if (typeof args === 'object') {
    if (args?.e) {
      args.e = args.e.message;
    }
    message += JSON.stringify(args);
  }
  if (typeof args === 'string') {
    message += args;
  }
  let err: Error;
  try {
    err = new Error(
      // 打点日志只显示 message
      urlParamsMap['env'] === 'log'
        ? JSON.stringify({
            name: msg,
            message,
            event_type: type,
            src: window.location.href,
            extra_info: window.weblog.oncallExtra,
          })
        : message
    );
  } catch (e) {
    err = new Error(message);
  }
  try {
    if (type === 'card_error') {
      // 错误上报 雷达3.8.24+已支持
      window.weblog.plugins.radar.captureException(err);
    } else {
      // 其他上报通过自定义事件上报
      window.weblog.plugins.radar.event({
        name: msg,
        message,
        event_type: type,
        src: window.location.href,
        extra_info: window.weblog.oncallExtra,
      });
    }
  } catch (err) {
    error('radar failed', err);
  }
};

export const performanceCollect = () => {
  window.addEventListener('load', () => {
    try {
      const timing = window.performance.timing;
      const ttfb = timing.responseStart - timing.requestStart; // 请求响应耗时
      const fb = timing.responseStart - timing.fetchStart; // 首字节
      const domReady = timing.domContentLoadedEventEnd - timing.fetchStart; // domReady
      const resource = timing.loadEventStart - timing.fetchStart; // 同步资源加载时长
      const initCost = performance.measure(
        'fetchBridgeCost',
        'fetchBridgeStart',
        'fetchBridgeEnd'
      ).startTime;
      const bridgeInitCost = performance.measure(
        'bridgeInitCost',
        'initBridgeStart',
        'fetchBridgeStart'
      ).duration;
      const bridgeCost = performance.measure(
        'fetchBridgeCost',
        'fetchBridgeStart',
        'fetchBridgeEnd'
      ).duration;
      const renderCost = performance.measure('showCost', 'fetchBridgeEnd', 'renderEnd').duration;
      const totalCost = initCost + bridgeCost + renderCost;
      radarCollect('c_init耗时', initCost);
      radarCollect('c_birdgeInit耗时', bridgeInitCost);
      radarCollect('c_birdge耗时', bridgeCost);
      radarCollect('c_render耗时', renderCost);
      radarCollect('c_total耗时', totalCost);
      radarCollect('c_ttfb', ttfb);
      radarCollect('c_首字节', fb);
      radarCollect('c_domReady', domReady);
      radarCollect('c_同步资源加载耗时', resource);
    } catch (e) {
      console.warn('performance: nomrak', e);
    }
  });
};
