import { bridge } from '@bridge';
import { LogParamsType, AdImpressionType, BridgeCallbackResponse } from '@interface';
import { radarEnd, radarStart } from '@log';
import { error } from './monitor';

export const track = async (
  params: LogParamsType
): Promise<void | BridgeCallbackResponse<null>> => {
  radarStart('logFailRate');
  try {
    const logParams: {
      actionType: number;
      payload?: string | { [key: string]: any };
      adTemplate?: string; // 聚合广告场景时，需要传递广告信息
      refreshType?: number; // 聚合广告刷新类型
      clickInfo?: {
        x: number;
        y: number;
      };
    } = {
      actionType: params.actionType,
      // payload: params.payload || '{}',
      clickInfo: params.clickInfo,
    };
    if (typeof params.payload === 'string') {
      logParams.payload = params.payload || '{}';
    } else {
      logParams.payload = JSON.stringify(params.payload || {});
    }
    if (params.adTemplate) {
      logParams.adTemplate = params.adTemplate;
    }
    if (params.refreshType) {
      logParams.refreshType = params.refreshType;
    }
    return await bridge.log(logParams);
  } catch (e) {
    radarEnd('logFailRate');
    error('log fail', {
      isBridgeError: true,
    });
  }
};
/**
 * 聚合打点给媒体上报adOrder
 */
export const adImpression = async (
  params: AdImpressionType
): Promise<void | BridgeCallbackResponse<null>> => {
  try {
    const adImpressionParams: AdImpressionType = {
      creativeId: params.creativeId,
      adStyle: params?.adStyle,
    };
    return await bridge.adImpression(adImpressionParams);
  } catch (e) {
    error('adImpression fail', {
      isBridgeError: true,
    });
  }
};
