import React, { FC, useState, useEffect, useRef, MouseEvent } from 'react';
import { bridge } from '@bridge';
import { getElementStyle, hexToRgba } from '@/common/utils';
import { error } from '@/common/log';
import './index.less';
import { useDownloadInfo } from './useDownloadInfo';
import { ItemClickType } from '@enum';
import { DownloadInfoProps, ButtonProps } from './interface';
const primaryColor = '#fe3666';
const Button = ({
  itemclicktype,
  texts = {
    adActionDescription: '',
    openAppLabel: '',
    installAppLabel: '',
  },
  backgroundColor = primaryColor,
  isDownload = false,
  extraClass = '',
  extraChildClass = '',
  buttonAnimation = 'static',
  animationDelay = 0,
  isWatering = false,
  wateringStyle = {},
  downloadingFormat = 'T...%',
  downloadPauseFormat = 'T%',
  stimulateText,
  onClick,
  creativeId,
  isMulti = false,
  progressBackgroundColor,
  style,
  unlog,
  getStatus,
}: ButtonProps) => {
  const canvas = useRef();
  const [geometrySize, setGeometrySize] = useState([]);
  const [showTextAnimation, setShowTextAnimation] = useState(!!stimulateText);
  const [isMessionComplete, setIsMessionComplete] = useState(false);
  const progressBgColor = progressBackgroundColor || wateringStyle?.rightColor || primaryColor;
  useEffect(() => {
    if (isWatering) {
      setGeometrySize([
        getElementStyle('button', 'width', 128),
        getElementStyle('button', 'height', 44),
      ]);
    }
    if (stimulateText) {
      bridge
        .rewardTaskStatus()
        .then((res) => {
          setIsMessionComplete(res.data.taskStatus === 1);
        })
        .catch((e) => {
          console.error('call rewardTaskStatus failed', { e });
        });
    }
  }, []);
  const downloadInfoOptions: DownloadInfoProps = {
    texts,
    isDownload,
  };

  if (isWatering) {
    downloadInfoOptions.watering = {
      canvas,
      styles: {
        width: geometrySize[0],
        height: geometrySize[1],
        fontSize: wateringStyle.fontSize || 12,
        fontWeight: wateringStyle.fontWeight || 400,
        rightColor: wateringStyle?.rightColor || backgroundColor || primaryColor,
        leftColor: wateringStyle?.leftColor || primaryColor,
      },
    };
  }
  if (downloadingFormat) {
    downloadInfoOptions.downloadingFormat = downloadingFormat;
  }
  if (downloadPauseFormat) {
    downloadInfoOptions.downloadPauseFormat = downloadPauseFormat;
  }
  if (creativeId) {
    downloadInfoOptions.creativeId = creativeId;
  }
  if (isMulti) {
    downloadInfoOptions.isMulti = isMulti;
  }
  const { btnText, isDownloading, progress, status } = useDownloadInfo(downloadInfoOptions);
  getStatus && getStatus({ btnText, isDownloading, progress, status });

  useEffect(() => {
    if (stimulateText && !isDownloading && !isMessionComplete) {
      setShowTextAnimation(true);
    } else {
      setShowTextAnimation(false);
    }
  }, [status, isMessionComplete]);
  return (
    <div
      id="button"
      className={`${buttonAnimation}-button button ${extraClass}`}
      itemclicktype={itemclicktype || ItemClickType.CARD_ACTION_BAR_CLICK}
      unlog={unlog || ''}
      style={{
        animationDelay: `${String(animationDelay)}s`,
        WebkitAnimationDelay: `${String(animationDelay)}s`,
        ...style,
      }}
      buttondetail={btnText}
      onClick={(e) => onClick && onClick(e)}
    >
      {/* 未下载(无边框，有背景色) */}
      {(!isDownload || !isDownloading || !isWatering) && (
        <div
          className={`wrap ${extraChildClass}`}
          style={{
            backgroundColor,
          }}
        >
          <div
            className={`btn-text normal-text ${showTextAnimation ? 'text-animation' : 'static'}`}
          >
            {btnText}
          </div>
          {showTextAnimation && (
            <div className="btn-text stimulate-text text-animation">{stimulateText}</div>
          )}
        </div>
      )}
      {/* 下载中 注水效果 */}
      {isWatering && (
        <div
          className={`wrap ${extraChildClass}`}
          style={{ backgroundColor: hexToRgba(progressBgColor, 0.5) }}
        >
          <div
            className="progress-bar"
            style={{ width: progress * 100 + '%', backgroundColor: progressBgColor }}
          />
          <canvas ref={canvas} className="canvas" />
        </div>
      )}
    </div>
  );
};

export default Button;
